.footer {
  width: 100%;
  min-height: 312px;
  margin-top: 60px;
  flex: 0 0 auto;

  &__content {
    display: flex;
    justify-content: space-between;
    height: 100%;
    margin-bottom: 30px;

    .footer__menu {
      width: 194px;
    }

    .footer__contacts {
      width: 500px;
    }

    .footer__social {
      width: 340px;
    }

    /*  .footer__menu,
    .footer__contacts,
    .footer__social {
      height: 100%;
      width: 100%;
    } */

    .footer__menu_title,
    .footer__contacts_title,
    .footer__social_title {
      font-style: normal;
      font-weight: normal;
      font-size: 48px;
      line-height: 125%;
      margin-bottom: 30px;
    }

    .footer__menu_block {
      display: flex;
      justify-content: space-between;

      .footer__links {
        list-style-type: none;
        display: grid;
        grid-template-rows: repeat(4, 1fr);
        row-gap: 20px;

        .footer__link {
          text-decoration: none;
          color: #888888;
          font-weight: normal;
          font-size: 16px;
          line-height: 18px;

          &:hover {
            color: #9d2f2f;
          }
        }
      }
    }

    .footer__contacts_block {
      display: flex;
      justify-content: space-between;
      color: #888888;
      font-weight: normal;
      font-size: 16px;
      line-height: 18px;

      .markets {
        width: 268px;
        display: flex;
        flex-direction: column;

        .markets__title {
          margin-bottom: 20px;
          font-weight: normal;
          font-size: 18px;
          line-height: 20px;
          color: #333333;
        }

        .markets__block {
          display: flex;
          flex-direction: row;
          justify-content: space-between;
        }

        &__topic {
          list-style-type: none;
          display: grid;
          grid-template-rows: repeat(2, 1fr);
          row-gap: 20px;
        }
      }

      .working-time {
        width: 184px;
        display: flex;
        flex-direction: column;

        .working-time__title {
          margin-bottom: 20px;
          font-weight: normal;
          font-size: 18px;
          line-height: 20px;
          color: #333333;
        }

        .working-time__block {
          display: flex;
          flex-direction: row;
          justify-content: space-between;
        }

        &__topic {
          list-style-type: none;
          display: grid;
          grid-template-rows: repeat(2, 1fr);
          row-gap: 20px;
        }
      }
    }

    .footer__social {
      display: flex;
      flex-direction: column;
      align-items: flex-start;

      &_topic {
        width: 228px;
        list-style-type: none;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        column-gap: 48px;
      }

      &_link {
        color: white;
        text-align: center;
        height: 44px;
        width: 44px;
        border-radius: 50%;
        background-color: #9d2f2f;
        font-size: 40px;

        &:hover {
          color: #fff;
          background: #333;
        }
      }
    }
  }

  .duplet__now {
    font-size: 18px;
    line-height: 20px;
    text-align: center;
  }
}

@import "./media/footer-media";